.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.login, .signup {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}
.input {
    font-size: 15px;
    padding: 10px 20px;
    margin-top: 10px;
    border-radius: 5px;
    width: 100%;
}
.btn {
    font-size: 20px;
    padding: 10px 20px;
    margin: 10px 10px 0 10px;
}
.link{
    text-decoration: none;
    color: black;
}
.button_holder {
    display: flex;
    flex-direction: row;
    justify-content: center;
}