.container {
    margin: 0;
    padding: 0;
}
.main {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: blanchedalmond;
    width: 100vw;
    height: 100vh;
}
.listContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(197, 157, 105);
    width: 400px;
    border-radius: 15px;
    border: 2px solid black;
    padding: 15px;
    margin: 10px;
}
.list {
    list-style-type: none;
}
.button {
    padding: 10px 20px;
    margin: 5px 20px;
    width: 100px;
}
.input {
    padding: 10px 20px;
    margin: 20px;
    width: 400px;
}
.link {
    text-decoration: none;
    color: black;
}